.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 250px;
  margin: 0 auto 30px;
  border-radius: 5px;
  overflow: hidden;
  border: 1px solid rgb(151, 151, 151);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2), 0 4px 6px rgba(0, 0, 0, 0.2);
}
.profileImg {
  margin: 30px 0;
  width: 100px;
  height: auto;
  border-radius: 50%;
  object-fit: contain;
  overflow: hidden;
}
.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: rgb(255, 255, 255);
  padding: 30px 0;
}
.avatar {
  max-width: 100%;
}
.name {
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 5px;
  color: rgb(64, 64, 64);
}
.tag {
  color: gray;
  margin-bottom: 5px;
}
.location {
  color: gray;
  margin-bottom: 5px;
}
.stats {
  display: flex;
  width: 100%;
  height: 70px;
  background-color: rgb(255, 255, 255);
}
.item {
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid rgb(151, 151, 151);

  overflow: hidden;
  background-color: rgb(212, 209, 209);
}
.label {
}
.quantity {
  font-weight: bold;
}
