.section {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  justify-content: center;
  width: 350px;
  margin: 0 auto 30px;
}
.item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 5px;
  flex: 1 0 70px;
  width: 100%;
  height: 70px;
  background-color: white;
  border-radius: 5px;
  overflow: hidden;
  border: 1px solid rgb(151, 151, 151);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2), 0 4px 6px rgba(0, 0, 0, 0.2);
}
.avatar {
  flex-basis: 60px;
  overflow: hidden;
  background-color: #ddf6e7;
  border: 1px solid rgb(151, 151, 151);
  border-radius: 5px;
}
.name {
  flex-basis: 60px;
  font-size: 22px;
}
.onlineStyle {
  margin: 20px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: green;
}
.offlineStyle {
  margin: 20px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: red;
}
